import client, { ClientResponse } from "@/services/client"

export interface Message {
  id?: string
  title?: string
  type?: string
  status?: string
  content?: string
  createtime?: string
  updatetime?: string
}

interface feedback {
  id?: string
  uid?: string
  question?: string
  imgUrl?: string
  mobile?: string
  answer?: string
  status?: string
  chuangjiantime?: string
  updatetime?: string
}

interface order {
  id?: string
  uid?: string
  order_id?: string
  order_no?: string
  currencytype?: string
  suanli?: string
  status?: string
  money?: string
  chuangjiantime?: string
  updatetime?: string
}

export interface MessageData {
  message?: Message
  feedback?: feedback
  orderNotice?: order
}

export default class MessageService {
  public static getMessage(): Promise<ClientResponse<MessageData>> {
    return client.get("/api/messageNewest.php").then(({ data }) => data)
  }

  public static getHomeMessages(): Promise<ClientResponse<Message[]>> {
    return client.get("/api/message.php?home=true").then(({ data }) => data)
  }
}
