import axios from "axios"
import { SERVER_URL } from "@/services/base"
import TokenContext from "@/services/token"

function transformResponse<T>(raw: string): ClientResponse<T> {
  const { status, msg: message, data } = JSON.parse(raw)
  return {
    status,
    message,
    data,
  }
}

export interface ClientResponse<T> {
  status: number
  message: string
  data: T
}

const client = axios.create({
  baseURL: SERVER_URL,
  transformResponse: [transformResponse],
})

client.interceptors.request.use((config) => {
  const token = TokenContext.getToken()
  if (token) {
    config.headers["token"] = token.token
  }
  return config
})

export default client
