<template>
  <div :class="prefixClassname('login')">
    <div :class="prefixClassname('login__header')">
      <div :class="prefixClassname('login__hi')">嗨！矿友们好</div>
      <div :class="prefixClassname('login__welcome')">欢迎使用创盈超算</div>
    </div>
    <van-form :class="prefixClassname('login__form')">
      <van-field
        :class="prefixClassname('login__field')"
        v-model.trim="state.mobile"
        name="mobile"
        placeholder="请输入手机号"
        :border="false"
      />
      <van-field
        :class="prefixClassname('login__field')"
        v-model.trim="state.password"
        name="password"
        placeholder="请输入密码"
        :border="false"
        :type="state.passwordHidden ? 'password' : 'text'"
        :right-icon="state.passwordHidden ? 'closed-eye' : 'eye-o'"
        @click-right-icon="state.passwordHidden = !state.passwordHidden"
      />
      <div :class="prefixClassname('login__submit')">
        <van-button
          round
          type="primary"
          :loading="loading"
          :disabled="submitDisabled"
          @click="onSubmit"
        >
          登录
        </van-button>
      </div>
    </van-form>
    <div :class="prefixClassname('login__footer')">
      <div>
        <Link to="/forgot">忘记密码</Link>
        <span :class="prefixClassname('login__link-separator')" />
        <Link to="/register">注册登录</Link>
      </div>
      <div :class="prefixClassname('login__user-agreement')">
        继续登录即代表同意
        <Link to="/agreements/service">《创盈超算用户协议》</Link>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, reactive, ref } from "vue"
import { Button, Field, Form, Toast } from "vant"
import Link from "../../components/Link"
import UserService from "@/services/user"
import _ from "lodash"
import TokenContext from "@/services/token"
import { useRouter } from "vue-router"

export default defineComponent({
  name: "UserLogin",
  components: {
    Link,
    [Button.name]: Button,
    [Form.name]: Form,
    [Field.name]: Field,
    [Field.name]: Field,
  },
  setup() {
    const loading = ref(false)
    const { push } = useRouter()
    const state = reactive({
      mobile: "",
      password: "",
      passwordHidden: true,
    })

    const submitDisabled = computed(() => _.isEmpty(state.mobile) || _.isEmpty(state.password))

    function onSubmit() {
      const { mobile, password, passwordAgain, code } = state
      loading.value = true
      UserService.loginUser({
        mobile,
        password,
        passwordAgain,
        code,
      }).then(({ status, data, message }) => {
        loading.value = false
        if (status !== 200) {
          Toast(message)
          return
        }
        TokenContext.setToken(data)
        push("/")
      })
    }

    return {
      loading,
      state,
      submitDisabled,
      onSubmit,
    }
  },
})
</script>

