<template>
  <van-notice-bar class="home-notice-bar" left-icon="volume-o" mode="link" :scrollable="false">
    <van-swipe
      vertical
      class="home-notice-bar__swipe"
      height="20"
      :autoplay="3000"
      :show-indicators="false"
    >
      <van-swipe-item v-for="message in messages" :key="message.id">
        {{ message.title }}
      </van-swipe-item>
    </van-swipe>
  </van-notice-bar>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { NoticeBar, Swipe, SwipeItem } from "vant"
import MessageService, { Message } from "@/services/message"

export default defineComponent({
  name: "HomeNoticeBar",
  components: {
    [NoticeBar.name]: NoticeBar,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
  },
  setup() {
    const messages = ref<Message[]>([])

    onMounted(() => {
      MessageService.getHomeMessages().then(({ data }) => {
        messages.value = data
      })
    })
    return {
      messages,
    }
  },
})
</script>

