import { RouteRecordRaw } from "vue-router"

const routes: Array<RouteRecordRaw> = [
  {
    path: "/orders",
    name: "OrderList",
    props: (route) => ({
      status: route.query.status,
    }),
    component: () => import(/* webpackChunkName: "order" */ "../views/order/OrderList.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/orders/:orderNo",
    name: "OrderDetail",
    props: (route) => ({
      orderNo: route.params.orderNo,
    }),
    component: () => import(/* webpackChunkName: "order" */ "../views/order/OrderDetail.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/orders/notifications",
    name: "OrderNotificationList",
    component: () =>
      import(/* webpackChunkName: "notifications" */ "../views/order/OrderNotificationList.vue"),
  },
]

export default routes
