interface UserToken {
  mobile: string
  token: string
}

export default class TokenContext {
  public static getToken(): UserToken {
    const token = localStorage.getItem("__userToken__")
    if (token) {
      return JSON.parse(token)
    }
    return { mobile: "", token: "" }
  }

  public static existToken(): boolean {
    const token = this.getToken()
    return token.token != ""
  }

  public static setToken(token: UserToken): void {
    localStorage.setItem("__userToken__", JSON.stringify(token))
  }

  public static removeToken(): void {
    localStorage.removeItem("__userToken__")
  }
}
