import axios from "axios"
import { API2_URL } from "@/services/base"

export interface Network {
  coin?: string
  ppsMiningEarnings?: string
  date?: string
}

export default class NetworkService {
  public static getNetwork(coin?: string): Promise<Network> {
    return axios.get(`${API2_URL}v1/coins/${coin}/network`).then(({ data }) => data)
  }
}
