
import { computed, defineComponent, onMounted, ref } from "vue"
import NetworkService, { Network } from "@/services/network"
import { Image } from "vant"

export default defineComponent({
  name: "ProductNetworkCard",
  components: {
    [Image.name]: Image,
  },
  props: {
    type: String,
  },
  setup(props) {
    const network = ref<Network>()

    const unitValue = computed(() => {
      if (props.type === "btc" || props.type === "bch") {
        return "T"
      } else if (props.type === "eth") {
        return "M"
      }
      return ""
    })

    const unit = computed(() => {
      if (props.type === "btc" || props.type === "bch") {
        return "BTC/T/天"
      } else if (props.type === "eth") {
        return "ETH/M/天"
      }
      return ""
    })

    onMounted(() => {
      NetworkService.getNetwork(props.type).then((nk) => {
        network.value = nk
      })
    })

    return {
      network,
      unitValue,
      unit,
    }
  },
})
