
import { defineComponent, onMounted, ref } from "vue"
import { Col, Row, Toast } from "vant"
import HomePlatformOperation from "@/views/home/HomePlatformOperation.vue"
import OperationService, { Operation } from "@/services/operation"

export default defineComponent({
  name: "HomePlatformOperations",
  components: {
    HomePlatformOperation,
    [Row.name]: Row,
    [Col.name]: Col,
  },
  setup() {
    const operation = ref<Operation>()

    function refreshOperation() {
      OperationService.getOperation().then(({ status, message, data }) => {
        if (status !== 200) {
          Toast(message)
          return
        }
        operation.value = data
      })
    }

    onMounted(refreshOperation)

    return {
      operation,
    }
  },
})
