
import { defineComponent, onMounted, ref, watch } from "vue"
import IndexLayout from "@/layouts/IndexLayout.vue" // @ is an alias to /src
import { Col, Icon, Image, NavBar, Row, Toast } from "vant"
import MeCard from "@/views/me/MeCard.vue"
import MeAssetCard from "@/views/me/MeAssetCard.vue"
import MeCellGroup from "@/views/me/MeCellGroup.vue"
import TokenContext from "@/services/token"
import UserService from "@/services/user"
import CustomerServicePopup from "@/components/CustomerServicePopup.vue"
import MeMiningEarningsNotice from "@/views/me/MeMiningEarningsNotice.vue"

export default defineComponent({
  name: "Me",
  components: {
    MeMiningEarningsNotice,
    CustomerServicePopup,
    MeCellGroup,
    MeAssetCard,
    MeCard,
    IndexLayout,
    [NavBar.name]: NavBar,
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Image.name]: Image,
  },
  setup() {
    const logged = ref(false)
    const loading = ref(false)
    const userInfo = ref({ nickname: "", avatar: "", mobile: "" })
    const customerServicePopupShow = ref(false)

    onMounted(() => {
      logged.value = TokenContext.existToken()
    })

    watch([logged], () => {
      if (logged.value) {
        loading.value = true
        UserService.getCurrentUser()
          .then(({ status, message, data }) => {
            if (status !== 200) {
              Toast(message)
              return
            }
            userInfo.value = data.userInfo
          })
          .finally(() => (loading.value = false))
      }
    })

    return {
      logged,
      loading,
      userInfo,
      customerServicePopupShow,
    }
  },
})
