import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router"
import base from "@/router/base"
import me from "@/router/me"
import order from "@/router/order"
import product from "@/router/product"
import asset from "@/router/asset"
import setting from "@/router/setting"
import feedback from "@/router/feedback"
import payment from "@/router/payment"
import partner from "@/router/partner"
import TokenContext from "@/services/token"
import agreement from "@/router/agreement"

const routes: Array<RouteRecordRaw> = [
  ...agreement,
  ...asset,
  ...base,
  ...me,
  ...order,
  ...product,
  ...setting,
  ...feedback,
  ...partner,
  ...payment,
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

router.beforeEach((to, from, next) => {
  // routeHistory
  if (to.meta.requiresAuth) {
    const isAuthenticated = TokenContext.existToken()
    if (isAuthenticated) {
      next()
    } else {
      next("/login")
    }
  } else {
    next()
  }
})

export default router
