import { RouteRecordRaw } from "vue-router"
import UserLogin from "@/views/user/UserLogin.vue"
import Home from "@/views/Home.vue"

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
  },
  {
    path: "/login",
    name: "Login",
    component: UserLogin,
  },
  {
    path: "/register",
    name: "Register",
    props: (route) => ({
      steps: route.query.steps,
      mobile: route.query.mobile,
      inviteCode: route.query.inviteCode,
    }),
    component: () => import(/* webpackChunkName: "user" */ "../views/user/UserRegister.vue"),
  },
  {
    path: "/forgot",
    name: "Forgot",
    component: () => import(/* webpackChunkName: "user" */ "../views/user/UserForgot.vue"),
  },
  {
    path: "/resets/password",
    name: "PasswordReset",
    component: () => import(/* webpackChunkName: "user" */ "../views/user/PasswordReset.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/resets/paypassword",
    name: "PaypasswordReset",
    component: () => import(/* webpackChunkName: "user" */ "../views/user/PaypasswordReset.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/invite",
    name: "invite",
    component: () => import(/* webpackChunkName: "invite" */ "../views/invite/Invite.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/about-us",
    name: "AboutUs",
    component: () => import(/* webpackChunkName: "aboutus" */ "../views/about-us/AboutUs.vue"),
  },
  {
    path: "/messages",
    name: "MessageList",
    component: () => import(/* webpackChunkName: "message" */ "../views/message/MessageList.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/notices",
    name: "Notice",
    component: () => import(/* webpackChunkName: "notice" */ "../views/notice/NoticeList.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/notices/:id",
    name: "NoticeDetail",
    component: () =>
      import(/* webpackChunkName: "notice" */ "../views/notice/NoticeListDetail.vue"),
    meta: { requiresAuth: true },
  },
]

export default routes
