
import { defineComponent } from "vue"
import { Cell, CellGroup, Icon, Image } from "vant"
import { useRouter } from "vue-router"

export default defineComponent({
  name: "MeCellGroup",
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Icon.name]: Icon,
    [Image.name]: Image,
  },
  setup() {
    const { push: route } = useRouter()
    return {
      route,
    }
  },
})
