<template>
  <van-popup
    class="customer-service-popup"
    :show="show"
    closeable
    @close="onClose()"
    @click-overlay="onClose()"
    @click-close-icon="onClose()"
  >
    <van-image
      height="160px"
      width="160px"
      :src="require('../assets/wechat-official-accounts.jpg')"
    ></van-image>
    <div class="customer-service-popup__tips1">添加创盈超算微信客服</div>
    <div class="customer-service-popup__tips2">如有疑问，请扫描添加客服微信随时咨询，谢谢</div>
  </van-popup>
</template>

<script>
import { defineComponent } from "vue"
import { Image, Popup } from "vant"

export default defineComponent({
  name: "CustomerServicePopup",
  components: {
    [Image.name]: Image,
    [Popup.name]: Popup,
  },
  props: {
    show: Boolean,
  },
  setup(props, { emit }) {
    function onClose() {
      emit("update:show", false)
    }

    return {
      onClose,
    }
  },
})
</script>

