<template>
  <div class="home-asset-swipe-card" @click="$router.push('/assets/detail')">
    <div class="home-asset-swipe-card__l1" @click.stop="1">
      <div class="home-asset-swipe-card__asset-title">{{ name }} 余额</div>
      <div class="home-asset-swipe-card__eye">
        <van-icon v-if="eye" name="eye-o" size="14" @click.stop="$emit('eye', false)" />
        <van-icon v-else name="closed-eye" size="14" @click.stop="$emit('eye', true)" />
      </div>
    </div>
    <div class="home-asset-swipe-card__l2">
      <div class="home-asset-swipe-card__asset-value">{{ balanceWithClosed }}</div>
      <div class="home-asset-swipe-card__earnings-yesterday">
        <div class="home-asset-swipe-card__earnings-yesterday__title">昨日收益</div>
        <div class="home-asset-swipe-card__earnings-yesterday__value">{{ yIncomeWithClosed }}</div>
      </div>
    </div>
    <div class="home-asset-swipe-card__balance-title">累计收益</div>
    <div class="home-asset-swipe-card__l3">
      <div class="home-asset-swipe-card__balance">{{ allIncomeWithClosed }}</div>
      <div class="home-asset-swipe-card__actions">
        <van-button
          size="small"
          round
          @click.stop="$router.push(`/assets/withdrawal?type=${type}`)"
        >
          转出
        </van-button>
        <van-button
          v-if="type === 'usdt'"
          size="small"
          round
          @click.stop="$router.push('/assets/topup')"
        >
          转入
        </van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent } from "vue"
import { Button, Icon } from "vant"

export default defineComponent({
  name: "HomeAssetSwipeCard",
  components: {
    [Button.name]: Button,
    [Icon.name]: Icon,
  },
  props: {
    type: String,
    name: String,
    eye: Boolean,
    allIncome: {
      type: [String, Number],
      default: 0,
    },
    yIncome: {
      type: [String, Number],
      default: 0,
    },
    balance: {
      type: [String, Number],
      default: 0,
    },
  },
  setup(props) {
    const allIncomeWithClosed = computed(() => (props.eye ? props.allIncome : "****"))
    const yIncomeWithClosed = computed(() => (props.eye ? props.yIncome : "****"))
    const balanceWithClosed = computed(() => (props.eye ? props.balance : "****"))
    return { allIncomeWithClosed, yIncomeWithClosed, balanceWithClosed }
  },
})
</script>

