<template>
  <div class="home__l1">
    <van-nav-bar :border="false" @click-right="$router.push('/messages')">
      <template #title>
        <van-skeleton :loading="logged && loading" title avatar>
          <home-profile
            :logged="logged"
            :avatar="userInfo.avatar"
            :nickname="userInfo.nickname"
          />
        </van-skeleton>
      </template>
      <template #right>
        <van-icon name="chat-o" size="16" />
      </template>
    </van-nav-bar>
    <home-asset-swiper />
    <!-- <home-product-categories />-->
    <home-notice-bar />
  </div>
  <div class="home__l2">
    <home-banner />
    <home-star-product />
    <home-recommend-product-list />
    <home-more-product-list />
    <home-platform-services />
    <home-platform-operations />
  </div>
  <bottom-logo />
</template>

<script lang="ts">
import { defineComponent, onMounted, ref, watch } from "vue"
import { Icon, Image, NavBar, PullRefresh, Skeleton, Toast } from "vant"
import HomeAssetSwiper from "@/views/home/HomeAssetSwiper.vue"
// import HomeProductCategories from "@/views/home/HomeProductCategories.vue"
import HomeNoticeBar from "@/views/home/HomeNoticeBar.vue"
import HomeStarProduct from "@/views/home/HomeStarProduct.vue"
import HomeBanner from "@/views/home/HomeBanner.vue"
import HomeRecommendProductList from "@/views/home/HomeRecommendProductList.vue"
import HomeMoreProductList from "@/views/home/HomeMoreProductList.vue"
import HomePlatformServices from "@/views/home/HomePlatformServices.vue"
import HomePlatformOperations from "@/views/home/HomePlatformOperations.vue"
import HomeProfile from "@/views/home/HomeProfile.vue"
import TokenContext from "@/services/token"
import UserService from "@/services/user"
import BottomLogo from "@/components/BottomLogo.vue"

export default defineComponent({
  name: "HomeBody",
  components: {
    BottomLogo,
    HomeProfile,
    HomePlatformOperations,
    HomePlatformServices,
    HomeMoreProductList,
    HomeRecommendProductList,
    HomeBanner,
    HomeStarProduct,
    HomeNoticeBar,
    // HomeProductCategories,
    HomeAssetSwiper,
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,
    [Image.name]: Image,
    [Skeleton.name]: Skeleton,
    [PullRefresh.name]: PullRefresh,
  },
  setup() {
    const logged = ref(false)
    const loading = ref(false)
    const userInfo = ref({ nickname: "", avatar: "" })

    onMounted(() => {
      logged.value = TokenContext.existToken()
    })

    function refreshHome() {
      if (logged.value) {
        loading.value = true
        UserService.getCurrentUser()
          .then(({ status, message, data }) => {
            if (status !== 200) {
              Toast(message)
              return
            }
            userInfo.value = data.userInfo
          })
          .finally(() => (loading.value = false))
      }
    }

    watch([logged], refreshHome)

    return {
      logged,
      loading,
      userInfo,
    }
  },
})
</script>
