import client, { ClientResponse } from "@/services/client"

interface UserInfo {
  mobile: string
  avatar: string
  nickname: string
}

interface CurrentUser {
  userInfo: UserInfo
}

interface RegisterUserForm {
  mobile?: string
  password?: string
  passwordAgain?: string
  paypassword?: string
  paypasswordAgain?: string
  code?: string
  inviteCode?: string
}

interface LoginUserForm {
  mobile?: string
  password?: string
}

interface SendCaptchaForm {
  mobile?: string
  type?: "register" | "find" | "withdraw" | "usdtAdd" | "btcAdd" | "ethAdd"
}

interface FindUserForm {
  mobile?: string
  password?: string
  passwordAgain?: string
  code?: string
}

interface ResetPaypasswordForm {
  password?: string
  passwordAgain?: string
  code?: string
}

interface UserToken {
  mobile: string
  token: string
}

interface UserAvatarResponse {
  imgUrl: string
}

export default class UserService {
  // 发送注册或者找回密码的验证码
  public static sendCaptcha(form: SendCaptchaForm): Promise<ClientResponse<void>> {
    return client.post("/users/code.php", form).then(({ data }) => data)
  }

  public static registerUser(form: RegisterUserForm): Promise<ClientResponse<void>> {
    return client.post("/users/register.php", form).then(({ data }) => data)
  }

  public static loginUser(form: LoginUserForm): Promise<ClientResponse<UserToken>> {
    return client.post("/users/login.php", form).then(({ data }) => data)
  }

  public static findUser(form: FindUserForm): Promise<ClientResponse<void>> {
    return client.post("/users/find.php", form).then(({ data }) => data)
  }

  public static resetPaypassword(form: ResetPaypasswordForm): Promise<ClientResponse<void>> {
    return client.post("/users/resetPaypassword.php", form).then(({ data }) => data)
  }

  public static checkMobile(mobile?: string): Promise<ClientResponse<void>> {
    return client.get(`/users/checkMobile.php?mobile=${mobile}`).then(({ data }) => data)
  }

  public static getCurrentUser(): Promise<ClientResponse<CurrentUser>> {
    return client.post("/users/getInfo.php").then(({ data }) => data)
  }

  public static modifyNickname(nickname: string): Promise<ClientResponse<void>> {
    return client
      .post("/users/nickname.php", {
        nickname,
      })
      .then(({ data }) => data)
  }

  public static updateAvatar(avatar: string): Promise<ClientResponse<UserAvatarResponse>> {
    return client
      .post("/users/avatar.php", {
        img: avatar,
      })
      .then(({ data }) => data)
  }
}
