import { RouteRecordRaw } from "vue-router"
import ProductHome from "@/views/product/ProductHome.vue"

const routes: Array<RouteRecordRaw> = [
  {
    path: "/products/home",
    name: "ProductHome",
    component: ProductHome,
  },
  {
    path: "/products/:productId",
    name: "ProductDetail",
    props: (route) => ({
      id: route.params.productId,
    }),
    component: () => import(/* webpackChunkName: "product" */ "../views/product/ProductDetail.vue"),
  },
  {
    path: "/products/home/recite",
    name: "Recite",
    component: () => import(/* webpackChunkName: "recite" */ "../views/home-recite/Recite.vue"),
  },
]

export default routes
