<template>
  <div class="home-recommend-product-list">
    <div class="home-recommend-product-list__header">
      <div class="home-recommend-product-list__header__title">
        <div class="home-recommend-product-list__header__title-value">为你推荐</div>
        <div class="home-recommend-product-list__header__title-icon">平台严选 万里挑一</div>
      </div>
    </div>
    <van-swipe
      class="home-recommend-product-list__swiper"
      :loop="false"
      :show-indicators="false"
      width="320"
    >
      <van-swipe-item v-for="(product, index) in products" :key="product.pid">
        <home-recommend-product-swipe-card
          :no="index + 1"
          :pid="product.pid"
          :producttype="product.producttype"
          :productname="product.productname"
          :shengyukucun="product.shengyukucun"
          :yujishouyi="product.yujishouyi"
          background="#ecf8fe"
        />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { Swipe, SwipeItem } from "vant"
import HomeRecommendProductSwipeCard from "./HomeRecommendProductSwipeCard.vue"
import ProductService, { Product } from "@/services/product"

export default defineComponent({
  name: "HomeRecommendProductList",
  components: {
    HomeRecommendProductSwipeCard,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
  },
  setup() {
    const products = ref<Product[]>([])
    onMounted(() => {
      ProductService.getRecommendProducts().then(({ data }) => (products.value = data))
    })
    return { products }
  },
})
</script>

