import { RouteRecordRaw } from "vue-router"

const routes: Array<RouteRecordRaw> = [
  {
    path: "/payments/methods",
    name: "PaymentMethodList",
    component: () =>
      import(/* webpackChunkName: "payment" */ "../views/payment/PaymentMethodList.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/payments/methods/:type",
    name: "PaymentMethod",
    props: (route) => ({
      type: route.params.type,
    }),
    component: () => import(/* webpackChunkName: "payment" */ "../views/payment/PaymentMethod.vue"),
    meta: { requiresAuth: true },
  },
]

export default routes
