<template>
  <div class="me-asset-card" @click.stop="route('/assets/detail')">
    <div class="me-asset-card__header">
      <van-row align="center" justify="space-between">
        <van-col class="me-asset-card__header__asset-name">资产余额</van-col>
        <!--        <van-col class="me-asset-card__header__asset-amount">
          <span>2348923.32948</span>
          <van-icon name="play" size="12" />
        </van-col>-->
      </van-row>
    </div>
    <div class="me-asset-card__content">
      <van-row gutter="40" class="me-asset-card__r1">
        <van-col span="12">
          <me-asset-card-balance name="ETH余额" :amount="wallet?.eth?.balance" />
        </van-col>
        <van-col class="van-hairline--left" span="12">
          <me-asset-card-balance name="BTC余额" :amount="wallet?.btc?.balance" />
        </van-col>
      </van-row>
      <div class="me-asset-card__r2">
        <me-asset-card-balance name="USDT余额" :amount="wallet?.usdt?.balance" />
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { Col, Icon, Row } from "vant"
import MeAssetCardBalance from "@/views/me/MeAssetCardBalance.vue"
import WalletService, { Wallet } from "@/services/wallet"

export default defineComponent({
  name: "MeAssetCard",
  components: {
    MeAssetCardBalance,
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
  },
  setup() {
    const wallet = ref<Wallet>({})

    function refreshWallet() {
      WalletService.getWallet().then(({ data }) => {
        wallet.value = data
      })
    }

    onMounted(refreshWallet)
    return {
      wallet,
    }
  },
})
</script>

