import client, { ClientResponse } from "@/services/client"

export interface Product {
  pid?: string
  pic?: string
  number?: string | number
  producttype?: string
  currencytype?: string
  productname?: string
  currency?: string
  productprice?: string
  payment?: string
  expense?: string
  // 功耗
  gonghao?: string
  // 算力
  suanli?: string
  // 有效算力
  youxiaosuanli?: string
  // 库存
  kucun?: string | number
  // 剩余库存
  shengyukucun?: string | number
  // 产品周期
  chanpinzhouqi?: string | number
  // 日收益
  rishouyi?: string | number
  // 预计收益
  yujishouyi?: string | number
  // 单位
  unit?: string | number
  // 电费
  dianfei?: string | number
  // 购买日期
  goumairiqi?: string | number
  // 上架日期
  shangjiariqi?: string | number
  // 服务日期
  fuwuriqi?: string | number
  // 风险提醒
  fengxiantixing?: string
  // 产品详情
  chanpinxiangqing?: string
  // 分红天数
  fenhongtianshu?: string | number
}

export interface Notice {
  id?: string
  title?: string
  type?: string
  status?: string
  content?: string
  createtime?: string
  updatetime?: string
}

export default class ProductService {
  public static getProducts(): Promise<ClientResponse<Product[]>> {
    return client.post("/product/productList.php").then(({ data }) => data)
  }

  public static getProduct(productId?: string): Promise<ClientResponse<Product>> {
    return client.get(`/product/productSelect.php?pid=${productId}`).then(({ data }) => data)
  }

  public static getStarProduct(): Promise<ClientResponse<Product[]>> {
    return client.get("/product/productStar.php").then(({ data }) => data)
  }

  public static getRecommendProducts(): Promise<ClientResponse<Product[]>> {
    return client.get("/product/productTuijian.php").then(({ data }) => data)
  }

  public static getNotice(): Promise<ClientResponse<Notice[]>> {
    return client.get("/api/message.php").then(({ data }) => data)
  }

  public static getNoticeDetail(type: string, id: string): Promise<ClientResponse<Notice>> {
    return client.get(`/api/messageDetail.php?type=${type}&id=${id}`).then(({ data }) => data)
  }
}
