
import { defineComponent, onMounted, ref } from "vue"
import { Swipe, SwipeItem } from "vant"
import HomeAssetSwipeCard from "@/views/home/HomeAssetSwipeCard.vue"
import WalletService, { Wallet } from "@/services/wallet"

export default defineComponent({
  name: "HomeAssetSwiper",
  components: {
    HomeAssetSwipeCard,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
  },
  setup() {
    const wallet = ref<Wallet>({})
    const eye = ref(true)

    function refreshWallet() {
      WalletService.getWallet().then(({ data }) => {
        wallet.value = data
      })
    }

    onMounted(refreshWallet)
    return {
      eye,
      wallet,
    }
  },
})
