<template>
  <div :class="['product-network-card', `product-network-card--${type}`]">
    <div class="product-network-card__btc-com">来自btc.com的理论每{{ unitValue }}收益</div>
    <div class="product-network-card__earnings">
      <div class="product-network-card__earnings__amount">{{ network?.ppsMiningEarnings }}</div>
      <div class="product-network-card__earnings__unit">{{ unit }}</div>
    </div>
    <div class="product-network-card__bottom">每日净挖矿所得等于每日挖矿产出减去算力费和电费</div>
    <van-image
      class="product-network-card__logo"
      height="80%"
      width="80px"
      :src="require(`../../assets/coins/${type}.png`)"
    />
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onMounted, ref } from "vue"
import NetworkService, { Network } from "@/services/network"
import { Image } from "vant"

export default defineComponent({
  name: "ProductNetworkCard",
  components: {
    [Image.name]: Image,
  },
  props: {
    type: String,
  },
  setup(props) {
    const network = ref<Network>()

    const unitValue = computed(() => {
      if (props.type === "btc" || props.type === "bch") {
        return "T"
      } else if (props.type === "eth") {
        return "M"
      }
      return ""
    })

    const unit = computed(() => {
      if (props.type === "btc" || props.type === "bch") {
        return "BTC/T/天"
      } else if (props.type === "eth") {
        return "ETH/M/天"
      }
      return ""
    })

    onMounted(() => {
      NetworkService.getNetwork(props.type).then((nk) => {
        network.value = nk
      })
    })

    return {
      network,
      unitValue,
      unit,
    }
  },
})
</script>
