<template>
  <div class="home-star-product">
    <div class="home-star-product__header">
      <div class="home-star-product__header__title">
        <div class="home-star-product__header__title-value">平台明星</div>
        <div class="home-star-product__header__title-icon">
          <van-icon name="fire" size="13" />
        </div>
      </div>
      <div class="home-star-product__header__type">
        <div class="home-star-product__header__type-icon">
          <van-icon name="notes-o" size="16" />
        </div>
        <div class="home-star-product__header__type-value">平台明星</div>
      </div>
    </div>
    <div class="home-star-product__content">
      <div class="home-star-product__card" @click.stop="push(`/products/${product.pid}`)">
        <div class="home-star-product__image">
          <van-image width="100" height="120" :src="product.pic" />
        </div>
        <div class="home-star-product__card--right">
          <div class="home-star-product__title">{{ product.productname }}</div>
          <div class="home-star-product__price">
            <div class="home-star-product__price-value">{{ product.productprice }}</div>
            <div class="home-star-product__price-unit">USDT/台</div>
          </div>
          <div class="home-star-product__quantity-progress">
            <van-progress
              :percentage="(product.shengyukucun / product.kucun) * 100"
              color="#8fdbff"
              :show-pivot="false"
            />
          </div>
          <div class="home-star-product__quantity">剩余台数: {{ product.shengyukucun }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { Icon, Image, Progress } from "vant"
import ProductService, { Product } from "@/services/product"
import { useRouter } from "vue-router"

export default defineComponent({
  name: "HomeStarProduct",
  components: {
    [Icon.name]: Icon,
    [Image.name]: Image,
    [Progress.name]: Progress,
  },
  setup() {
    const { push } = useRouter()
    const product = ref<Product>({})
    onMounted(() => {
      ProductService.getStarProduct().then(({ data }) => (product.value = data[0]))
    })
    return {
      push,
      product,
    }
  },
})
</script>

