
import { defineComponent, ref } from "vue"
import IndexLayout from "@/layouts/IndexLayout.vue"
import { PullRefresh } from "vant"
import UserService from "@/services/user"
import HomeBody from "@/views/HomeBody.vue"

export default defineComponent({
  name: "Home",
  components: {
    HomeBody,
    IndexLayout,
    [PullRefresh.name]: PullRefresh,
  },
  setup() {
    const pullRefreshLoading = ref(false)
    const pullRefreshDatetime = ref(Date.now())

    function pullRefreshHome() {
      pullRefreshDatetime.value = Date.now()
      pullRefreshLoading.value = true
      UserService.getCurrentUser().finally(() => {
        pullRefreshLoading.value = false
      })
    }

    return {
      pullRefreshLoading,
      pullRefreshDatetime,
      pullRefreshHome,
    }
  },
})
