<template>
  <div class="home-more-product-list">
    <div class="home-more-product-list__header">
      <div class="home-more-product-list__header__title">运营情况</div>
    </div>
    <div class="home-more-product-list__content">
      <van-row :gutter="10">
        <van-col :span="8">
          <home-platform-operation
            index="1"
            background="linear-gradient(to right, #02a0e7, #00bdf7)"
            title="平台服务人数"
            suffix="人"
          >
            {{ operation?.people }}
          </home-platform-operation>
        </van-col>
        <van-col :span="8">
          <home-platform-operation
            index="2"
            background="linear-gradient(to right, #017df1, #2f9ff8)"
            title="已经安全运行"
            suffix="天"
          >
            {{ operation?.days }}
          </home-platform-operation>
        </van-col>
        <van-col :span="8">
          <home-platform-operation
            index="3"
            background="linear-gradient(to right, #3e77fe, #5991fe)"
            title="累计交易额"
            suffix="万元"
          >
            {{ operation?.money }}
          </home-platform-operation>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { Col, Row, Toast } from "vant"
import HomePlatformOperation from "@/views/home/HomePlatformOperation.vue"
import OperationService, { Operation } from "@/services/operation"

export default defineComponent({
  name: "HomePlatformOperations",
  components: {
    HomePlatformOperation,
    [Row.name]: Row,
    [Col.name]: Col,
  },
  setup() {
    const operation = ref<Operation>()

    function refreshOperation() {
      OperationService.getOperation().then(({ status, message, data }) => {
        if (status !== 200) {
          Toast(message)
          return
        }
        operation.value = data
      })
    }

    onMounted(refreshOperation)

    return {
      operation,
    }
  },
})
</script>

