
import { defineComponent, nextTick, onMounted, ref } from "vue"
import ProductNetworkCard from "@/views/product/ProductNetworkCard.vue"
import { Swipe, SwipeItem } from "vant"

export default defineComponent({
  name: "ProductHomeRealtimeNetwork",
  components: {
    ProductNetworkCard,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
  },
  setup() {
    const swiperRef = ref()
    onMounted(() => {
      nextTick(() => {
        swiperRef.value.resize()
      })
    })

    return { swiperRef }
  },
})
