<template>
  <van-notice-bar>
    <van-swipe
      vertical
      class="me-mining-earnings-notice__swipe"
      :autoplay="3000"
      :show-indicators="false"
    >
      <van-swipe-item>BTC实时价格：{{ btcNetwork?.ppsMiningEarnings }} BTC/T/天</van-swipe-item>
      <!-- <van-swipe-item>BCH实时价格：{{ bchNetwork?.ppsMiningEarnings }} BCH/T/天</van-swipe-item>-->
      <van-swipe-item>ETH实时价格：{{ ethNetwork?.ppsMiningEarnings }} ETH/M/天</van-swipe-item>
    </van-swipe>
  </van-notice-bar>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { NoticeBar, Swipe, SwipeItem } from "vant"
import NetworkService, { Network } from "@/services/network"

export default defineComponent({
  name: "MeMiningEarningsNotice",
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [NoticeBar.name]: NoticeBar,
  },
  setup() {
    const btcNetwork = ref<Network>()
    const bchNetwork = ref<Network>()
    const ethNetwork = ref<Network>()
    onMounted(() => {
      NetworkService.getNetwork("btc").then((nk) => (btcNetwork.value = nk))
      NetworkService.getNetwork("bch").then((nk) => (bchNetwork.value = nk))
      NetworkService.getNetwork("eth").then((nk) => (ethNetwork.value = nk))
    })

    return {
      btcNetwork,
      bchNetwork,
      ethNetwork,
    }
  },
})
</script>

<style lang="scss">
@import "../../styles/prefix";
@import "../../styles/variables";

.me-mining-earnings-notice {
  &__swipe {
    height: 40px;
    line-height: 40px;
  }
}
</style>
