import { RouteRecordRaw } from "vue-router"
import Me from "@/views/me/Me.vue"
import WalletIndex from "@/views/me/wallet/WalletIndex.vue"

const routes: Array<RouteRecordRaw> = [
  {
    path: "/me",
    name: "Me",
    component: Me,
    meta: { requiresAuth: true },
  },
  {
    path: "/wallets/index",
    name: "WalletIndex",
    component: WalletIndex,
    meta: { requiresAuth: true },
  },
]

