import client, { ClientResponse } from "@/services/client"

interface WalletBalance {
  balance?: number
  yIncome?: number
  allIncome?: number
  pIncome?: number
  rent?: number
}

export interface Wallet {
  btc?: WalletBalance
  eth?: WalletBalance
  usdt?: WalletBalance
}

export interface Fee {
  btc?: string
  eth?: string
  usdt?: string
}

export default class WalletService {
  public static getWallet(): Promise<ClientResponse<Wallet>> {
    return client.get("/product/myWallet.php").then(({ data }) => data)
  }

  public static getFee(): Promise<ClientResponse<Fee>> {
    return client.get("/product/fee.php").then(({ data }) => data)
  }
}
