<template>
  <slot />
  <van-tabbar v-model="active" @change="nextRoute" :placeholder="placeholder">
    <van-tabbar-item name="/home" icon="home-o">首页</van-tabbar-item>
    <van-tabbar-item name="/products/home" icon="bag-o">产品</van-tabbar-item>
    <van-tabbar-item name="/me" icon="user-o">我的</van-tabbar-item>
  </van-tabbar>
</template>

<script>
import { computed, defineComponent, onMounted, ref } from "vue"
import { Tabbar, TabbarItem } from "vant"
import { useRouter } from "vue-router"

export default defineComponent({
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
  },
  setup() {
    const { currentRoute, push: nextRoute } = useRouter()
    const active = ref("/home")

    onMounted(() => {
      currentRoute.value.path
      if (currentRoute.value.path !== active.value) {
        active.value = currentRoute.value.path
      }
    })

    const placeholder = computed(() => active.value === "/home" || active.value === "/me")

    return {
      active,
      placeholder,
      nextRoute,
    }
  },
})
</script>
