<template>
  <van-swipe class="home-asset-swiper" :loop="false" :show-indicators="false" width="350">
    <van-swipe-item>
      <home-asset-swipe-card
        name="ETH"
        type="eth"
        :all="wallet?.eth?.allIncome"
        :yIncome="wallet?.eth?.yIncome"
        :balance="wallet?.eth?.balance"
        :eye="eye"
        @eye="(e) => (eye = e)"
      />
    </van-swipe-item>
    <van-swipe-item>
      <home-asset-swipe-card
        name="BTC"
        type="btc"
        :all="wallet?.btc?.allIncome"
        :yIncome="wallet?.btc?.yIncome"
        :balance="wallet?.btc?.balance"
        :eye="eye"
        @eye="(e) => (eye = e)"
      />
    </van-swipe-item>
    <van-swipe-item>
      <home-asset-swipe-card
        name="USDT"
        type="usdt"
        :all="wallet?.usdt?.allIncome"
        :yIncome="wallet?.usdt?.yIncome"
        :balance="wallet?.usdt?.balance"
        :eye="eye"
        @eye="(e) => (eye = e)"
      />
    </van-swipe-item>
  </van-swipe>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { Swipe, SwipeItem } from "vant"
import HomeAssetSwipeCard from "@/views/home/HomeAssetSwipeCard.vue"
import WalletService, { Wallet } from "@/services/wallet"

export default defineComponent({
  name: "HomeAssetSwiper",
  components: {
    HomeAssetSwipeCard,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
  },
  setup() {
    const wallet = ref<Wallet>({})
    const eye = ref(true)

    function refreshWallet() {
      WalletService.getWallet().then(({ data }) => {
        wallet.value = data
      })
    }

    onMounted(refreshWallet)
    return {
      eye,
      wallet,
    }
  },
})
</script>

<style lang="scss">
@import "../../styles/prefix";
@import "../../styles/variables";

.home-asset-swiper {
  padding: $padding-xs 0;

  .van-swipe-item {
    box-sizing: border-box;
    padding: $padding-xs;
  }
}
</style>
