<template>
  <index-layout>
    <div class="me">
      <div class="me__l1">
        <van-nav-bar title="我的" :border="false">
          <template #right>
            <van-row gutter="14">
              <van-col>
                <van-icon name="service-o" size="22px" @click="customerServicePopupShow = true" />
              </van-col>
              <van-col>
                <van-icon name="setting-o" size="22px" @click="$router.push('/setting')" />
              </van-col>
            </van-row>
          </template>
        </van-nav-bar>
        <div class="me__user-info">
          <van-image width="48" height="48" :src="userInfo.avatar" round />
          <div class="me__user-info__c2">
            <span class="me__user-nickname">{{ userInfo.nickname }}</span>
            <span class="me__user-mobile">{{ userInfo.mobile }}</span>
          </div>
        </div>
        <van-row gutter="10">
          <van-col span="8">
            <me-card type="order" name="我的订单" @click="$router.push('/orders')"></me-card>
          </van-col>
          <van-col span="8">
            <me-card
              type="wallet"
              name="我的钱包"
              @click="$router.push('/assets/detail')"
            ></me-card>
          </van-col>
          <van-col span="8">
            <me-card type="invite" name="我的邀请" @click="$router.push('/invite')"></me-card>
          </van-col>
        </van-row>
      </div>
      <me-mining-earnings-notice />
      <div class="me__l2">
        <me-asset-card />
        <me-cell-group />
      </div>
    </div>
  </index-layout>
  <customer-service-popup v-model:show="customerServicePopupShow" />
</template>

<script lang="ts">
import { defineComponent, onMounted, ref, watch } from "vue"
import IndexLayout from "@/layouts/IndexLayout.vue" // @ is an alias to /src
import { Col, Icon, Image, NavBar, Row, Toast } from "vant"
import MeCard from "@/views/me/MeCard.vue"
import MeAssetCard from "@/views/me/MeAssetCard.vue"
import MeCellGroup from "@/views/me/MeCellGroup.vue"
import TokenContext from "@/services/token"
import UserService from "@/services/user"
import CustomerServicePopup from "@/components/CustomerServicePopup.vue"
import MeMiningEarningsNotice from "@/views/me/MeMiningEarningsNotice.vue"

export default defineComponent({
  name: "Me",
  components: {
    MeMiningEarningsNotice,
    CustomerServicePopup,
    MeCellGroup,
    MeAssetCard,
    MeCard,
    IndexLayout,
    [NavBar.name]: NavBar,
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Image.name]: Image,
  },
  setup() {
    const logged = ref(false)
    const loading = ref(false)
    const userInfo = ref({ nickname: "", avatar: "", mobile: "" })
    const customerServicePopupShow = ref(false)

    onMounted(() => {
      logged.value = TokenContext.existToken()
    })

    watch([logged], () => {
      if (logged.value) {
        loading.value = true
        UserService.getCurrentUser()
          .then(({ status, message, data }) => {
            if (status !== 200) {
              Toast(message)
              return
            }
            userInfo.value = data.userInfo
          })
          .finally(() => (loading.value = false))
      }
    })

    return {
      logged,
      loading,
      userInfo,
      customerServicePopupShow,
    }
  },
})
</script>
