<template>
  <div class="shared-product-pane">
    <div class="shared-product-pane__header">
      <div class="shared-product-pane__title">算力租赁</div>
      <div class="shared-product-pane__subtitle">押金不可退</div>
    </div>
    <van-pull-refresh v-model="loading" @refresh="refreshProducts">
      <div class="shared-product-pane__content">
        <product-card
          v-for="product in products"
          :key="product.pid"
          :pid="product.pid"
          :productname="product.productname"
          :shengyukucun="product.shengyukucun"
          :chanpinzhouqi="product.chanpinzhouqi"
          :rishouyi="product.rishouyi"
          :productprice="product.productprice"
          :currencytype="product.currencytype"
        />
      </div>
      <div v-if="!loading" class="shared-product-pane__bottom">我是有底线的</div>
      <bottom-logo />
    </van-pull-refresh>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import ProductCard from "@/views/product/ProductCard.vue"
import { PullRefresh } from "vant"
import ProductService, { Product } from "@/services/product"
import BottomLogo from "@/components/BottomLogo.vue"

export default defineComponent({
  name: "SharedProductPane",
  components: {
    BottomLogo,
    ProductCard,
    [PullRefresh.name]: PullRefresh,
  },
  setup() {
    const loading = ref(false)
    const products = ref<Product[]>([])

    function refreshProducts() {
      loading.value = true
      ProductService.getProducts()
        .then(({ data }) => (products.value = data))
        .finally(() => (loading.value = false))
    }

    onMounted(refreshProducts)

    return {
      loading,
      products,
      refreshProducts,
    }
  },
})
</script>

